export default function galleryCardMarkup(images, galleryEl) {
  const markup = images.map(
    ({
      largeImageURL,
      webformatURL,
      tags,
      likes,
      views,
      comments,
      downloads
    }) => {
      return `
  <div class="photo-card">
    <a class='photo-card-thumb' href=${largeImageURL}>
        <img 
            src="${webformatURL}" 
            alt="${tags}" 
            loading="lazy" 
        />
    </a>
    <ul class="info">
        <li class="info-item">
        <b>Likes</b>
        ${likes}
        </li>
        <li class="info-item">
            <b>Views</b>
            ${views}
        </li>
        <li class="info-item">
            <b>Comments</b>
            ${comments}
        </li>
        <li class="info-item">
            <b>Downloads</b>
            ${downloads}
        </li>
    </ul>
    </div>`;
    }
  ).join('');

  galleryEl.innerHTML = markup;
}
